import React, { useState } from "react";
import ReactApexChart from "react-apexcharts";
import Chart from "react-apexcharts";
import "./skillCard.scss";
const SkillCard = ({ img, title, percentage, color }) => {
  const [chartOptions, setChartOptions] = useState({
    series: [percentage],
    options: {
      chart: {
        width: 150,
        height: 150,
        type: "radialBar",
        toolbar: {
          show: false,
        },
      },
      plotOptions: {
        radialBar: {
          startAngle: 0,
          endAngle: 360,
          hollow: {
            margin: 0,
            size: "60%",
            background: "#fff",
            image: undefined,
            imageOffsetX: 0,
            imageOffsetY: 0,
            position: "front",
            dropShadow: {
              enabled: true,
              top: 3,
              left: 0,
              blur: 4,
              opacity: 0.24,
            },
          },
          track: {
            show: false,
          },

          dataLabels: {
            show: true,
            name: {
              offsetY: -10,
              show: true,
              color: "#888",
              fontSize: "17px",
            },
            value: {
              formatter: function (val) {
                return parseInt(val) + "%";
              },
              color: "#111",
              fontSize: "36px",
              show: true,
            },
          },
        },
      },
      fill: {
        type: "gradient",
        gradient: {
          shade: "dark",
          type: "horizontal",
          shadeIntensity: 0.5,
          gradientToColors: ["#b720d3"],
          inverseColors: true,
          opacityFrom: 1,
          opacityTo: 1,
          stops: [0, 100],
        },
      },
      stroke: {
        lineCap: "round",
        width: 1,
      },
      labels: [title],
    },
  });
  return (
    <div className="skill-card" data-aos="fade-up" data-aos-duration="1500">
      <div className="skill-card-top">
        <div className="skill-card-img">
          <img src={img} alt="" />
        </div>
      </div>
      <div className="skill-card-bottom">
        <ReactApexChart
          options={chartOptions.options}
          series={chartOptions.series}
          type="radialBar"
          height={300}
        />
      </div>
    </div>
  );
};

export default SkillCard;
